<template>
  <div class="login">
    <!-- el-from 是表单组件
      :label-option 设置label对齐方式 left
      Label-width 设置label的宽度 -->
    <!-- model 表单绑定的数据 -->
    <el-form
      :label-position="labelPosition"
      :label-width="labelWidth"
      :model="loginFrom"
      ref="userForm"
    >
      <!-- 
        el-from-item 需要的是el-from 的子组件
        label: 填写label信息
        label-width 设置当前 el-from-item 的 label的宽度
       -->
      <el-form-item :rules="usernameRules" prop="username" label="用户名">
        <el-input v-model="loginFrom.username"></el-input>
      </el-form-item>

      <el-form-item :rules="passwordRules" prop="password" label="密码：">
        <el-input v-model="loginFrom.password"></el-input>
      </el-form-item>
      <el-button @click="onSubmit('userForm')" size="medium" type="primary"
        >登录</el-button
      >
    </el-form>
  </div>
</template>

<script>
import { login } from "../../api/login.js";
import { set } from "../../utils/localStrage.js";
export default {
  name: "Login",
  data() {
    return {
      userForm: "userForm",
      loginFrom: {
        username: "",
        password: "",
      },
      labelPosition: "right",
      labelWidth: "80px",
      usernameRules: [
        { required: true, message: "请输入用户名", trigger: "blur" },
        { min: "4", message: "输入内容不能小于4", trigger: "blur" },
        { max: "6", message: "输入内容不能大于6", trigger: "blur" },
        {
          pattern: /\w{4,6}/,
          message: "请输入4-6位数字字母下划线",
          trigger: "blur",
        },
      ],
      passwordRules: [
        { required: true, message: "请输入用户名", trigger: "blur" },
        { min: "4", message: "输入内容不能小于4", trigger: "blur" },
        { max: "6", message: "输入内容不能大于6", trigger: "blur" },
        {
          pattern: /\w{6,16}/,
          message: "请输入6-16位数字字母下划线",
          trigger: "blur",
        },
      ],
    };
  },
  mounted() {},
  methods: {
    onSubmit(userForm) {
      /**
       * this.$refs[ref] 获取绑定元素的dom元素
       * Form Method
       */
      this.$refs[userForm].validate((valid) => {
        if (valid) {
          this.login();
        }
      });
    },
    login() {
      this.$store.dispatch("setuser", this.loginFrom).then((data) => {
        console.log(data);
          this.$message({
            message: "登录成功!!!",
            type: "success",
          });
          setTimeout(() => {
            let redirct = this.$route.query.redirct;
            if (redirct) {
              this.$router.push(redirct);
            } else {
              this.$router.push("/home");
            }
          }, 500);
        
       
        
      });
      // login(this.loginFrom).then((result) => {
      // if (result.code == 1) {
      //   let token = result.token;
      //   let username = result.username;
      //   let user = {
      //     token,
      //     username,
      //   };
      //   set("user", JSON.stringify(user));
      //   this.$store.dispatch("setuser", user);
      //   this.$message({
      //     message: "登录成功!!!",
      //     type: "success",
      //   });

      //   setTimeout(() => {
      //     this.$router.push("/home");
      //   }, 500);
      // 问题: home detail shopCAr 需要使用token
      // 解决 将token 共享
      //1: 分析 使用到token组件是否是关系型组件通信
      //2: 非关系的组件通信
      //3: vuex

      //问题 如何将token 存储到vuex中
      // 问题 页面刷新vuex 数据回到初始化 如何持久化存储
      // 提示登录成功
      // 跳转到首页
      //
      // });
    },
  },
};
</script>

<style lang="less">
.login {
  width: 400px;
  margin: 80px auto;
  box-shadow: 0px 0px 3px rgba(0, 0, 0, 0.3);
  padding: 30px;
}
</style>